body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.social {
  margin: 0 0 5px 0;
  display: block;
  -webkit-flex-direction: row;
          flex-direction: row;
  text-align: center;
}

.social button {
  background-color: rgba(0,0,0,0);
  border-color: rgba(0,0,0,0);
}

.social button:hover {
  cursor: pointer;
}

.App-header {
  z-index: 1;
  position: fixed;
  width: 100%;
  box-shadow: 0px 5px 4px rgba(0,0,0, .3);
  background-color: #bf2a2a;
  background-color: #282c34;
  min-height: 8vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.project {
  vertical-align: center;
  background-color: rgba(40,44,68,.6);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  box-shadow:         inset 0 0 90px rgba(0,0,0, .6);
  transition: 1.1s;

  -webkit-filter: sepia(100%);

          filter: sepia(100%);
}

.project p {
  opacity: 0;
  transition: .4s;
}

.project a {
  opacity: 0;
  transition: .4s;
  color: #61dafb;
}
.project:hover {
  background-color: rgba(40,44,52,1);
  box-shadow:         inset 0 0 13px rgba(0,0,0,0);
  transition: .4s;
  -webkit-filter: sepia(0%);
          filter: sepia(0%);
}
.project:hover p {
  opacity: 1;
  transition: .9s;
}
.project:hover a {
  opacity: 1;
  transition: .9s;
}

.projectdiv {
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1em;
}

.unused {
  background-color: #282c34;
  color:white;
}

.aboutdiv {
  padding: 23vh 20% 0 20%;
  width: 1fr;
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
  grid-gap: 0em;
  color:black;
  background-color: #f2c777;
}

.about h1 {
  width: inherit;
  margin: 0 0 1vh 0;
}
.about h3 {
  width: inherit;
  margin: 0;
}
.about p {
  padding: 0 7% 1em 7%;
  margin: 0;
}

.textbox {
  background-color: inherit;
  border-radius: inherit;
}

.industry {
  text-align: center;
  padding: 1em 0 2em 0;
}

.industry ul {
  margin: 0;
  text-align: left;
  display: inline-block;
}

.industry h2 {
  margin: 0;
  padding: 10px;
  background-color: #282c34;
  color:white;
}
.industry h3 {
  margin: 0;
  padding: 0;
  background-color: #f2c777;
}
.industry h6 {
  margin: 0;
  padding: 0;
  background-color: #f2c777;
}
.mia {
  bottom: 0;
  width: 100%;
  background-color: #282c34;
  margin: 1em 0 0 0;
  padding: 1em 0 1em 0;
}
svg {
  padding: 0 5px 0 5px;
  -webkit-filter: invert(100%);
  filter: invert(100%);
}
.project {
  background-image: url("/backdrop.svg");
}

